body .home{
    align-content: center;
    align-items: center;
    border: none;
    bottom: 0;
    position: fixed;
    display: flex;
    flex-direction: column;
    height: 87%;
    justify-content: center;
    overflow-x: hidden;
    overflow-y: auto;
    right: 0;
    width: 85%;
    z-index: 100;
}

.container-banner-home {
    width: 90%;
}

.banner-home {
    background: linear-gradient(85.39deg, rgba(1, 145, 206, 0.4) 0%, rgba(1, 145, 206, 0.331) 63.75%, rgba(165, 65, 243, 0.256) 100%);
    border-radius: 30px;
    display: flex;
    height: 15rem;
    justify-content: space-between;
    margin-bottom: 3rem;
    overflow: hidden;
    width: 100%;
}

.banner-info {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin-left: 3rem;
    margin-right: -5rem;
    padding: 2rem 0;
    width: 430.46px;
}

.banner-title {
    color: #0191CE;
    font-size: 27px;
    font-weight: 900;
    letter-spacing: 0.05em;
    line-height: 32px;
    margin: 0;
    max-height: 96px;
    overflow: scroll;
}

.banner-title::-webkit-scrollbar { display: none; }

.banner-hour {
    align-items: center;
    display: flex;
    height: 2rem;
}

.banner-date {
    align-items: center;
    display: flex;
    height: 2rem;
}

.hour {
    color: #005390;
    font-size: 20px;
    font-weight: 600;
    letter-spacing: 0.05em;
    line-height: 23px;
    padding-left: .5rem;
}

.date {
    color: #005390;
    font-size: 20px;
    font-weight: 600;
    letter-spacing: 0.05em;
    line-height: 23px;
    padding-left: .5rem;
}

.cards-home {
    align-items: center;
    display: flex;
    justify-content: space-between;
    width: 90%;
}

.container-home {
    border-radius: 25px;
    height: 15rem;
    width: 30%;
}

.glass-card {
    align-items: center;
    background: linear-gradient(136.75deg, rgba(33, 33, 33, 0.15) 0%, rgba(33, 33, 33, 0.06) 100%);
    border-radius: 25px;
    display: flex;
    flex-direction: column;
    height: 100%;
    justify-content: center;
    overflow: hidden;
    width: 100%;
}

.ruc {
    background: linear-gradient(316.75deg, rgba(1, 145, 206, 0.65) 0%, rgba(1, 145, 206, 0.65) 47.64%, rgba(79, 65, 243, 0.325) 100%);
}

.direccion {
    background: linear-gradient(4.49deg, rgba(1, 145, 206, 0.65) 1.58%, rgba(1, 145, 206, 0.65) 48.99%, rgba(165, 65, 243, 0.325) 101.1%);
}

.correo {
    background: linear-gradient(43.25deg, rgba(1, 145, 206, 0.65) 0%, rgba(1, 145, 206, 0.65) 47.64%, rgba(165, 65, 243, 0.325) 100%);
}

.icon-info {
    align-items: center;
    display: flex;
    height: 83px;
}

.card-info {
    align-items: center;
    color: #FFFFFF;
    display: flex;
    font-weight: 900;
    height: 72px;
    letter-spacing: 0.05em;
    line-height: 24px;
    margin: 1rem 0;
    overflow: scroll;
    text-align: center;
}

.card-info::-webkit-scrollbar { display: none; }

.info-ruc{
    align-items: center;
    font-size: 20px;
}

.info-email {
    align-items: center;
}

.info-direccion, .info-email {
    font-size: 16px;
    padding: 0 1rem;
}

@media only screen and (max-width: 1000px) {

    .container-home {
        height: 13rem;
    }

    .info-direccion, .info-email {
        font-size: 11px;
    }

}

@media only screen and (max-width: 960px) {
    /* Ocultar SideBar */
    body .home {
        width: 100%;
        z-index: 0;
    }
    
}

@media only screen and (max-width: 860px) {

    .banner-info {
        margin-left: 1.5rem;
        margin-right: -3rem;
    }

}

@media only screen and (max-width: 740px) {

    .banner-img {
        width: 18rem;
    }

    .cards-home {
        flex-direction: column;
    }

    .container-home {
        border-radius: 10px;
        height: 3.5rem;
        margin-bottom: 2rem;
        margin-top: -1.5rem;
        width: 80%;
    }

    .glass-card {
        border-radius: 10px;
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
    }

    .icon-info img{
        height: 50px;
        padding-left: 1rem;
        width: 50px;
    }

    .card-info {
        align-items: center;
        display: flex;
        height: 100%;
        margin: 0;
        padding: 0 .7rem 0 1.5rem;
        text-align: start;
        width: 100%;
    }
    
    .ruc {
        background: linear-gradient(43.25deg, rgba(1, 145, 206, 0.65) 0%, rgba(1, 145, 206, 0.65) 47.64%, rgba(165, 65, 243, 0.325) 100%);
    }

    .direccion {
        background: linear-gradient(43.25deg, rgba(1, 145, 206, 0.65) 0%, rgba(1, 145, 206, 0.65) 47.64%, rgba(165, 65, 243, 0.325) 100%);
    }

}

@media only screen and (max-width: 730px) {

    .banner-img {
        width: 15rem;
    }

}

@media only screen and (max-width: 475px) {

    .banner-home {
        min-width: 315px;
    }

    .banner-img {
        display: none;
    }

    .banner-info {
        align-items: center;
        margin-left: 1.5rem;
        margin-right: 1.5rem;
    }

    .banner-title {
        text-align: center;
    }

}

@media only screen and (max-height: 580px) {
    body .home {
        height: 83%;
        justify-content: flex-start;
    }
}

@media only screen and (max-height: 500px) {
    body .home {
        height: 80%;
    }
}

@media only screen and (max-width: 480px) {

    .cards-home {
        min-width: 315px;
    }

    .container-home, .glass-card {
        min-width: 315px;
        width: 100%;
    }
}

@media only screen and (max-width: 360px) {

    .info-direccion {
        font-size: 10px;
    }
}

@media only screen and (max-height: 350px) {
    body .home {
        z-index: 0;
    }
}